
import React, { useEffect, useState } from "react";
import { login, logout, handleCallback, getUser } from "./AuthService";

function App() {
  const [user, setUser] = useState(null);
  const [products, setProducts] = useState([]);
  const [orders, setOrders] = useState([]);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    
    if (window.location.pathname === "/auth/callback") {
      setLoading(true);
      handleCallback().then(u => {
        setLoading(false);
        
        getUser().then(current => setUser(current));
        
        window.history.replaceState({}, document.title, "/");
      }).catch(err => {
        setLoading(false);
        console.error("Callback error:", err);
      });
    } else {
     
      getUser().then(u => {
        if (u) setUser(u);
      }).catch(e => console.error(e));
    }
  }, []);

  const fetchProducts = async () => {
    if (!user) { alert("Por favor inicia sesin"); return; }
    const token = user.access_token;
    try {
      const res = await fetch(process.env.REACT_APP_MS_PRODUCTS_URL + "/products", {
        headers: { Authorization: `Bearer ${token}` }
      });
      if (!res.ok) {
        const txt = await res.text();
        throw new Error(`Status ${res.status}: ${txt}`);
      }
      const data = await res.json();
      setProducts(data);
    } catch (e) {
      console.error(e);
      alert("Error al obtener products: " + e.message);
    }
  };

  const fetchOrders = async () => {
    if (!user) { alert("Por favor inicia sesin"); return; }
    const token = user.access_token;
    try {
      const res = await fetch(process.env.REACT_APP_MS_ORDERS_URL + "/orders", {
        headers: { Authorization: `Bearer ${token}` }
      });
      if (!res.ok) {
        const txt = await res.text();
        throw new Error(`Status ${res.status}: ${txt}`);
      }
      const data = await res.json();
      setOrders(data);
    } catch (e) {
      console.error(e);
      alert("Error al obtener orders: " + e.message);
    }
  };

  return (
    <div style={{ padding: 20, fontFamily: "Arial, sans-serif" }}>
      <h1>Cliente React  Cognito (Hosted UI)</h1>
      {loading && <p>Procesando callback de autenticacin...</p>}

      {!user ? (
        <div>
          <p>No has iniciado sesin.</p>
          <button onClick={login}>Iniciar sesin (Cognito Hosted UI)</button>
          <p style={{ marginTop: 12, color: "#666" }}>
            Al iniciar sesin se abrir la ventana de AWS Cognito. Usa las credenciales configuradas en el User Pool.
          </p>
        </div>
      ) : (
        <div>
          <div style={{ marginBottom: 12 }}>
            <b>Usuario:</b> {user.profile?.email || user.profile?.username || user.profile?.sub}
          </div>
          <div style={{ marginBottom: 12 }}>
            <button onClick={logout}>Cerrar sesin</button>
          </div>

          <div style={{ marginBottom: 12 }}>
            <button onClick={fetchProducts}>Cargar Products (protegido)</button>
            <button onClick={fetchOrders} style={{ marginLeft: 8 }}>Cargar Orders (protegido)</button>
          </div>

          <div style={{ marginTop: 8 }}>
            <h3>Products</h3>
            <pre style={{ background: "#f4f4f4", padding: 8 }}>{JSON.stringify(products, null, 2)}</pre>
          </div>

          <div style={{ marginTop: 8 }}>
            <h3>Orders</h3>
            <pre style={{ background: "#f4f4f4", padding: 8 }}>{JSON.stringify(orders, null, 2)}</pre>
          </div>
        </div>
      )}
    </div>
  );
}

export default App;
